import React, { Component } from "react";
import "./Features.css";
import { features } from "../../portfolio";
import { Fade } from "react-reveal";

class FeatureSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div>
        {features.data.map((feature, i) => {
          if (i % 2 == 0) {
            return (
              <div className="features-main-div">
                <Fade left duration={2000}>
                  <div className="features-image-div">
                    {<img
                      src={require(`../../assests/images/${feature.imagePath}`)}
                    ></img>}
                  </div>
                </Fade>

                <div className="features-text-div">
                  <Fade right duration={1000}>
                    <h1 className="features-heading" style={{ color: theme.text }}>
                      {feature.title}
                    </h1>
                  </Fade>
                  <Fade right duration={2000}>
                    <div>
                      {feature.skills.map((featureSentence) => {
                        return (
                          <p
                            className="subTitle features-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {featureSentence}
                          </p>
                        );
                      })}
                    </div>
                  </Fade>
                </div>
              </div>
            );
          }
          else {
            return (
              <div className="features-main-div">
                <div className="features-text-div">
                  <Fade left duration={1000}>
                    <h1 className="features-heading" style={{ color: theme.text }}>
                      {feature.title}
                    </h1>
                  </Fade>
                  <Fade left duration={2000}>
                    <div>
                      {feature.skills.map((featureSentence) => {
                        return (
                          <p
                            className="subTitle features-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {featureSentence}
                          </p>
                        );
                      })}
                    </div>
                  </Fade>
                </div>

                <Fade right duration={2000}>
                  <div className="features-image-div">
                    {<img
                      src={require(`../../assests/images/${feature.imagePath}`)}
                    ></img>}
                  </div>
                </Fade>
              </div>
            );
          }
        })}
      </div>
    );
  }
}

export default FeatureSection;
