import React, { Component } from "react";
import "./Installs.css";
import { installs } from "../../portfolio";
import { Fade } from "react-reveal";

class InstallSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div>
        {installs.data.map((install) => {
          return (
            <div className="installs-main-div">
              <div className="installs-text-div">
                <Fade right duration={1000}>
                  <h1 className="installs-heading" style={{ color: theme.text }}>
                    {install.title}
                  </h1>
                </Fade>
                <Fade right duration={2000}>
                  <div>
                    {install.skills.map((installSentence) => {
                      return (
                        <p
                          className="subTitle installs-text"
                          style={{ color: theme.secondaryText }}
                        >
                          {installSentence}
                        </p>
                      );
                    })}
                  </div>
                </Fade>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default InstallSection;
