import React, { Component } from "react";
import "./Designs.css";
import { designs } from "../../portfolio";
import { Fade } from "react-reveal";

class DesignSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div>
        {designs.data.map((design, i) => {
          if (i % 2 == 0) {
            return (
              <div className="designs-main-div">
                <Fade left duration={2000}>
                  <div className="designs-image-div">
                    {<img
                      src={require(`../../assests/images/${design.imagePath}`)}
                    ></img>}
                  </div>
                </Fade>

                <div className="designs-text-div">
                  <Fade right duration={1000}>
                    <h1 className="designs-heading" style={{ color: theme.text }}>
                      {design.title}
                    </h1>
                  </Fade>
                  <Fade right duration={2000}>
                    <div>
                      {design.skills.map((designSentence) => {
                        return (
                          <p
                            className="subTitle designs-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {designSentence}
                          </p>
                        );
                      })}
                    </div>
                  </Fade>
                </div>
              </div>
            );
          } else {
            return (
              <div className="designs-main-div">
                <div className="designs-text-div">
                  <Fade left duration={1000}>
                    <h1 className="designs-heading" style={{ color: theme.text }}>
                      {design.title}
                    </h1>
                  </Fade>
                  <Fade left duration={2000}>
                    <div>
                      {design.skills.map((designSentence) => {
                        return (
                          <p
                            className="subTitle designs-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {designSentence}
                          </p>
                        );
                      })}
                    </div>
                  </Fade>
                </div>

                <Fade right duration={2000}>
                  <div className="designs-image-div">
                    {<img
                      src={require(`../../assests/images/${design.imagePath}`)}
                    ></img>}
                  </div>
                </Fade>
              </div>
            );
          }
        })}
      </div>
    );
  }
}

export default DesignSection;
