import React, { Component } from "react";
import "./Members.css";
import { members } from "../../portfolio";
import { Fade } from "react-reveal";

class MemberSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="members-overall-div">
        {members.data.map((member, i) => {
          if (i % 2 == 0) {
            return (
              <div className="members-text-div">
                <Fade right duration={1000}>
                  <h1 className="members-heading" style={{ color: theme.text }}>
                    {member.title}
                  </h1>
                </Fade>
                <Fade right duration={2000}>
                  <div>
                    {member.skills.map((memberSentence) => {
                      return (
                        <p
                          className="subTitle members-text"
                          style={{ color: theme.secondaryText }}
                        >
                          {memberSentence}
                        </p>
                      );
                    })}
                  </div>
                </Fade>
              </div>
            );
          }
          else {
            return (
              <div className="members-text-div-right">
                <Fade left duration={1000}>
                  <h1 className="members-heading" style={{ color: theme.text }}>
                    {member.title}
                  </h1>
                </Fade>
                <Fade left duration={2000}>
                  <div>
                    {member.skills.map((memberSentence) => {
                      return (
                        <p
                          className="subTitle members-text"
                          style={{ color: theme.secondaryText }}
                        >
                          {memberSentence}
                        </p>
                      );
                    })}
                  </div>
                </Fade>
              </div>
            );
          }
        })}
      </div>
    );
  }
}

export default MemberSection;
